import React, { Component } from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { StyledPage, Heading, SimpleDiv } from "../Config/Styles";

import { posts } from "../Config/Types";

interface Props {
  posts: posts;
}
interface State {}

const StyledItem = styled.div`
  display: flex;
  justify-content: space-between;
  border-bottom: 1px dotted ${(props) => `${props.theme.text}`};
  p {
    color: ${(props) => `${props.theme.text}`};
  }
`;

export default class Archive extends Component<Props, State> {
  state = {};

  posts = this.props.posts.map(({ id, title, date, content }, index) => {
    return (
      <div key={index}>
        <Link
          to={{
            pathname: `/posts/${id}`,
            state: { id, title, date, content },
          }}
        >
          <StyledItem>
            <h2>{title}</h2>
            <p>{date}</p>
          </StyledItem>
        </Link>
        <br />
      </div>
    );
  });

  render() {
    return (
      <SimpleDiv>
        <StyledPage>
          <Heading>
            <h1>Archive</h1>
          </Heading>
          <br />
          {this.posts}
        </StyledPage>
      </SimpleDiv>
    );
  }
}
